body {
  font-family: 'myriadpro-regular', sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  max-width: 1400px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
}

*,
*:before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
}

.clearfix::after {
  display: block;
  clear: both;
  content: '';
}

.svg-sprites {
  position: absolute;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  opacity: 0;
  top: 0;
  left: 0;
  border: 0;
}

.header {
  height: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 7.14% 1fr 7.14%;
  grid-template-rows: 40vh 60vh;
  grid-template-areas: '. title-bar .' '. content-bar .';
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  background: url("../img/img/header.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.header::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-color: black;
  opacity: .4;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.title-bar {
  grid-area: title-bar;
  position: relative;
}

.title-bar__title {
  font-family: 'myriadpro-bold', sans-serif;
  font-size: 202.2px;
  line-height: 1.2;
  letter-spacing: -0.05em;
  color: #ffffff;
}

.title-bar__title:first-letter {
  color: #ffc600;
}

.title-bar__description {
  font-family: 'myriadpro-regular', sans-serif;
  font-size: 18px;
  line-height: 1.2;
  color: #ffffff;
}

.title-bar__description:first-letter {
  color: #ffc600;
}

.title-bar__link {
  font-family: 'myriadpro-regular', sans-serif;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #ffc600;
}

.content-bar {
  grid-area: content-bar;
  position: relative;
  background: url("../img/img/header-content.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
